
  .preview {
    flex: 1;
    margin-bottom: 20px;
    padding: 20px;
    background: #d5ecf6;
    border: 1px solid #d5ecf6;
    border-radius: 0px;
    margin: 0;
  }

  .vb_button {
    background-color: #d5ecf6;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    border: none;
    color: rgb(10, 1, 1);
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
    margin-top: 12px;
    margin-bottom: 0px;
    cursor: pointer;
  }